import React, { Fragment, Component } from 'react';
import { Form, Container, Row, Col } from 'react-bootstrap';
import * as emailjs from 'emailjs-com';
import * as EmailValidator from 'email-validator';
import working_dev from '../img/working_dev.jpg';

const initialState = {
  name: '',
  email: '',
  subject: '',
  message: '',
  nameError: '',
  emailError: '',
  subjectError: '',
  mesError: ''
};

class Contact extends Component {
  state = initialState;

  handleSubmit(e) {
    e.preventDefault();
    const { name, email, subject, message } = this.state;
    let isValid = this.validateForm();

    if (isValid) {
      let templateParams = {
        name: name,
        from_name: email,
        to_name: 'phantran197@gmail.com',
        subject: subject,
        message_html: message
      };

      emailjs.send(
        'gmail',
        'template_IM4GpOfN',
        templateParams,
        'user_U6zPtO0IgtyhmCJUcwqZ2'
      );
      this.resetForm();
      alert('Message sent!');
    }
  }

  validateForm() {
    let nError = '';
    let sError = '';
    let eError = '';
    let mError = '';
    if (this.state.name === '') {
      nError = 'Please enter your name';
    }

    if (!EmailValidator.validate(this.state.email)) {
      eError = 'Please enter a valid email';
    }

    if (this.state.subject === '') {
      sError = 'Please enter the subject';
    }

    if (this.state.message === '') {
      mError = 'Please enter your message';
    }

    if (nError || sError || eError || mError) {
      this.setState({
        nameError: nError,
        subjectError: sError,
        emailError: eError,
        mesError: mError
      });
      return false;
    }

    return true;
  }

  resetForm(e) {
    this.setState(initialState);
  }

  handleChange = (param, e) => {
    this.setState({ [param]: e.target.value });
  };

  render() {
    return (
      <Fragment>
        <Container fluid={false}>
          <Row>
            <Col md={6} className="mt-5 pt-5 pb-0 mb-0">
              <img
                className="img-fluid"
                src={working_dev}
                alt="working developer"
              />
            </Col>
            <Col md={6} className="col-lg-6 mt-5 pt-5 mb-5">
              <Row className="justify-content-left pt-5 ml-5 pl-5">
                {' '}
                <a href="https://www.linkedin.com/in/phan-tran/">
                  <i className="fab fa-linkedin-in fa-4x contact-social-icons">
                    <h3>Connect with me on Linkedin</h3>
                  </i>
                </a>
              </Row>

              <Row className="justify-content-left mt-5 ml-5 pl-5">
                <a href="mailto:phantran197@gmail.com">
                  <i className="fas fa-envelope fa-4x contact-social-icons">
                    <h3>Drop me an email</h3>
                  </i>
                </a>
              </Row>
            </Col>
          </Row>
        </Container>
      </Fragment>
    );
  }
}

export default Contact;
